.raindrops { 
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-image: url(../../../../img/bg.jpg);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
	overflow: hidden;
}
.raindrops:before{
	content: "";
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	background-color: rgba(0,0,0,0.5);
}
html,body{
	height: 100%;
}
#raindrops{
	position: relative;
	height: 100%;
}